.jumbotron {
    width: 100%;
    text-align: center;
    margin-top: 80px;
    background-image: url("https://raw.githubusercontent.com/duncanfgmoore/clicky-game/master/public/images/buckingham-palace.jpg");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    height: 400px;
    padding-top: 50px;
    font-family: 'Old Standard TT', serif;
  }
  
  .jumbotronText {
    width: 65%;
    margin-left: auto;
    margin-right: auto;
    color: black;
    font-size: 48px;
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 10px;
  }

